import { send_cmd } from "./cmd";
async function btcCmd(cmd, tag, content) {
    return await send_cmd("Bitcoind", { cmd, content }, tag);
}
export async function get_info(tag) {
    return await btcCmd("GetInfo", tag);
}
export async function test_mine(tag, blocks, address) {
    if (address) {
        return await btcCmd("TestMine", tag, { blocks, address });
    }
    return await btcCmd("TestMine", tag, { blocks });
}
